.reviews{
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  .faq__top{
    width: 670px;
  }
  .container{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__inner{
    flex-grow: 1;
    padding: 50px 0 100px;
    position: relative;
  }
  &__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: 36px;
    max-width: calc(100% - 430px);
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 423px;
  }
  &__rows{
    margin-top: 36px;
    @include grid100gap(20px);
  }
}

.site-item{
  background-color: #fff;
  padding: 37px 25px;
  border-radius: 35px 0 35px 0;
  margin: 20px 60px 0 0;
  &__img{
    height: 38px;
    display: flex;
    align-items: center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__bottom{
    margin-top: 20px;
    display: flex;
  }
}

.review-card{
  display: grid;
  grid-template-columns: calc(100% - 328px) 328px;
  padding: 20px 32px 20px 27px;
  border: 1px solid #bebebe;
  border-radius: 3px;
  &__left{
    padding-right: 28px;
  }
  &__right{
    border-left: 1px solid #bebebe;
    padding: 28px 0 28px 40px;
    display: flex;
    align-items: center;
  }
  &__link{
    height: 35px;
    padding: 0 24px;
    border-radius: 3px;
    text-transform: unset;
    font-weight: 300;
    flex-shrink: 0;
  }
  &__img{
    flex-grow: 1;
    margin-right: 20px;
    img{
      max-width: 100%;
      max-height: 50px;
      display: block;
    }
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__name{
    font-size: 19px;
    line-height: 120%;
    font-weight: 500;
  }
  .rating-block{
    margin-left: 25px;
    &__num{
      display: none;
    }
  }
  &__date{
    font-size: 14px;
    line-height: 130%;
    margin-top: 5px;
    font-weight: 100;
    letter-spacing: 0.01em;
    color: #0c0927;
  }
  &__desc{
    margin-top: 5px;
    font-size: 15px;
    line-height: 22px;
    font-weight: 300;
    letter-spacing: 0.01em;
    padding-right: 10px;
    max-height: 66px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 7px;
      height: 7px;
    }
    &::-webkit-scrollbar-track {
      background-color: #cbcbcb;
      border-radius: 3.5px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: #2bb2ed;
      border-radius: 3.5px;
    }
  }
}
