.about{
  &__text{
    margin-top: 50px;
  }
}

.about-gallery{
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 80px / 3));
  gap: 40px;
}

.gallery-item{
  &._big{
    grid-column: 1/3;
    grid-row: 1/3;
  }
  &__img{
    @include imgCover(62.5%);
    display: block;
    border-radius: 3px;
    min-height: 100%;
  }
}
