.certificates{
  .faq__top{
    width: 900px;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 150px / 4));
    gap: 40px 50px;
    margin-top: 55px;
  }
}

.certificate-card{
  padding: 31px;
  border: 1px solid #bebebe;
  border-radius: 3px;
  display: block;
  &__img{
    display: block;
    @include imgCover(145%);
    border-radius: 3px;
  }
}
