.catalog-item{
  padding-bottom: 50px;
  &__inner{
    display: grid;
    grid-template-columns: 500px calc(100% - 545px);
    gap: 45px;
  }
}

.gallery{
  &__img{
    display: block;
    @include imgCover(93.4%);
    border-radius: 10px;
  }
  &__big{
    .swiper{
      border-radius: 10px;
    }
  }
  &__small{
    margin-top: 17px;
    padding: 0 46px;
    position: relative;
    .swiper{
      width: calc(100% + 15px);
      &-slide{
        width: 25%;
        padding-right: 15px;
        cursor: pointer;
      }
    }
    .gallery__img{
      padding-bottom: 100%;
    }
  }
  .swiper-button{
    background: $gradient;
    width: 33px;
    height: 33px;
    border-radius: 50%;
    @include center;
    margin: 0;
    top: 50%;
    transform: translateY(-50%);
    img{
      width: 18px;
    }
    &-prev{
      left: 0;
    }
    &-next{
      right: 0;
    }
  }
}

.catalog-info{
  min-height: 100%;
  background-color: #f4f5f7;
  border: 2px solid #ffffff;
  border-radius: 33px;
  padding: 25px;
  display: flex;
  flex-direction: column;
  .tab._active{
    z-index: 3;
  }
  .tab-content{
    box-shadow: 0px 10px 38px 0px rgba(0,0,0,0.05);
    border-radius: 3px;
    background-color: #fff;
    padding: 26px;
    margin-top: -5px;
    position: relative;
    z-index: 2;
    flex-grow: 1;
    &._active{
      display: flex;
      flex-direction: column;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: 40px;
  }
}

.services{
  padding: 50px 0 115px;
  &__top{
    text-align: center;
    padding: 0 60px;
  }
}

.slider__wrap{
  overflow: hidden;
}

.works-slider,
.services-slider{
  margin-top: 36px;
  position: relative;
  .swiper{
    &-nav{
      display: flex;
      align-items: center;
      justify-content: space-between;
      left: 0;
      bottom: 100%;
      position: absolute;
      width: 100%;
      margin-bottom: 45px;
    }
    &-button{
      position: static;
      margin: 0;
      width: 46px;
      height: 17px;
      img{
        width: 100%;
        display: block;
      }
    }
  }
}

.services-slider{
  .swiper{
    &-container{
      width: calc(100% + 16px);
    }
    &-slide{
      width: 25%;
      padding-right: 16px;
    }
  }
}

.service-card{
  position: relative;
  max-height: 333px;
  overflow: hidden;
  &._active{
    .service-card__content{
      color: #ffffff;
      background-color: #62b6da;
    }
    .service-card__links{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__content{
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 25px 25px 0;
    transition: all .3s;
  }
  &__title{
    font-size: 20px;
    line-height: 125%;
    font-weight: 500;
    font-family: $font2;
  }
  &__img{
    @include imgCover(100%);
  }
  &__links{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    overflow-y: auto;
    @include scroll;
    height: calc(100% - 25px);
    width: calc(100% + 50px);
    padding: 5px 25px 10px;
    margin-left: -25px;
    a{
      font-size: 16px;
      line-height: 150%;
      letter-spacing: 0.01em;
      color: #fefeff;
      &:hover{
        opacity: .8;
      }
    }
    ul{
      li{
        padding-left: 10px;
      }
      a{
        font-size: 14px;
      }
    }
  }
}


