.portfolio{
  .faq__top{
    width: 1040px;
  }
  &__grid{
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 72px / 4));
    gap: 24px;
  }
}

.certificate-card,
.portfolio-card{
  position: relative;
  &:hover{
    cursor: pointer;
    &:before,
    &:after{
      opacity: 1;
    }
  }
  &:before,
  &:after{
    content: '';
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &:before{
    position: absolute;
    background-color: rgba(#363636,.5);
    border-radius: 3px;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
  }
  &:after{
    @include centerAbsolute;
    width: 55px;
    height: 34px;
    background-color: #363636;
    border-radius: 17px;
    background-image: url(../img/search.png);
    @include bgImgSize(14px);
    z-index: 3;
  }
}

.portfolio-card{
  &._zoom{
    .portfolio-modal{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__img{
    @include imgCover(71.7%);
    border-radius: 3px;
  }
}

.portfolio-modal{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 30;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &__bg{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: $vh;
    background-color: rgba(#363636,.5);
  }
  &__dialog{
    border-radius: 3px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 10;
    width: 494px;
    max-height: calc(100 * var(--vh) - 40px);
    max-width: calc(100% - 20px);
    background-color: #fff;
    display: flex;
    flex-direction: column;
  }
  &__img{
    border-radius: 3px 3px 0 0;
    overflow: hidden;
    img{
      width: 100%;
      display: block;
      border-radius: inherit;
    }
  }
  &__desc{
    flex-shrink: 0;
    padding: 16px 32px;
    font-size: 15px;
    line-height: 133%;
  }
  &__close{
    position: absolute;
    width: 46px;
    height: 46px;
    @include center;
    background-color: #fff;
    border-radius: 50%;
    top: 5px;
    right: 5px;
    z-index: 3;
    img{
      width: 20px;
    }
  }
}
