.main{
  padding: 53px 0 45px;
  background-color: #ebecee;
  .container{
    padding: 0 15px;
  }
}

.main-slider{
  overflow: hidden;
  position: relative;
  .swiper-button{
    width: 92px;
    height: 56px;
    border-radius: 28px;
    @include center;
    img{
      width: 38px;
    }
    &.swiper-button-disabled{
      background: rgba(#fff,.4);
      opacity: 1;
    }
    &-prev{
      left: 20px;
    }
    &-next{
      right: 20px;
    }
  }
}

.main-banner{
  min-height: 589px;
  position: relative;
  display: flex;
  align-items: flex-end;
  padding: 58px 156px;
  color: #fff;
  &:after{
    border-radius: 72px;
    @media screen and (min-width: 993px){
      display: none;
    }
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: 72px;
    }
  }
  &__content{
    max-width: 550px;
    z-index: 3;
    position: relative;
  }
  &__title{
    font-size: 40px;
    line-height: 105%;
    font-weight: 500;
    letter-spacing: 0.02em;
  }
  &__desc{
    margin-top: 15px;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.02em;
    color: #d8d8d8;
  }
  &__nav{
    display: flex;
    margin-top: 15px;
  }
  &__btn{
    padding: 0 43px;
    height: 52px;
    border-radius: 26px;
    font-weight: 700;
    span{
      font-size: 13px;
    }
  }
}

.print{
  padding: 45px 0 90px;
  background-color: #ebecee;
  &__title{
    text-align: center;
    max-width: 740px;
    margin: 0 auto;
  }
  &__items{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 1250px;
    margin: 0 auto;
  }
}

.print-card{
  width: 25%;
  margin: 45px 0 0;
  padding: 0 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__img{
    position: relative;
    @include center;
    width: 171px;
    height: 171px;
    background-color: #ffa745;
    border-radius: 50%;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__link{
    position: absolute;
    bottom: 12px;
    left: 50%;
    transform: translateX(-50%);
    font-weight: 700;
    padding: 0 25px;
    height: 34px;
    border-radius: 17px;
    span{
      font-size: 13px;
    }
  }
  &__title{
    font-size: 17px;
    line-height: 112%;
    margin-top: 16px;
  }
}

.order{
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  padding: 50px 0 40px;
  background-color: #f6f7f9;
  &__top{
    text-align: center;
  }
  &__desc{
    margin-top: 13px;
  }
  &__fields{
    width: 1075px;
    max-width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 17px;
    margin: 32px auto 0;
  }
  .fg{
    input{
      background-color: #ffffff;
      box-shadow: 3px 4px 9px 0px rgba(72,39,9,0.05);
      border-radius: 7px;
      border-color: #fff;
      padding-left: 40px;
      &:focus{
        border-color: #20adea;
      }
    }
    &__icon{
      position: absolute;
      left: 15px;
      top: 50%;
      transform: translateY(-50%);
      width: 20px;
      height: 20px;
      @include center;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
  &__nav{
    margin-top: 23px;
    @include center;
  }
  &__btn{
    padding: 0 27px;
    border-radius: 4px;
    height: 50px;
    span{
      font-size: 14px;
    }
  }
}

.file-input2{
  @include center;
  margin-top: 20px;
  &__title{
    font-size: 15px;
    line-height: 160%;
    color: #5b5b5b;
  }
  &__btn{
    margin-left: 25px;
    padding: 0 21px;
    height: 41px;
    background-color: #cfcfcf;
    border-radius: 4px;
    font-weight: 500;
    text-transform: uppercase;
    color: #ffffff;
    position: relative;
    span{
      font-size: 12px;
    }
    img{
      width: 13px;
      margin: 0 7px -2px 0;
    }
  }
}
