.pagination{
  margin-top: 32px;
  &__items{
    display: flex;
  }
  &__item{
    width: 45px;
    height: 40px;
    background-color: #dadbdd;
    border-radius: 20px;
    @include center;
    margin: 0 7px 0 0;
    font-size: 15px;
    svg{
      width: 1.0625em;
      height: auto;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: #fff;
      background-image: linear-gradient(102.0deg, #ffb15a 13%, #ffa540 88%);
    }
    &._btn{
      padding: 0 1.5em;
    }
  }
}


