.header{
  padding-bottom: 27px;
  margin-bottom: 33px;
  background-color: #ebecee;
  box-shadow: 0px 0px 38px 0px rgba(0,0,0,0.09);
  position: relative;
  z-index: 20;
  &__inner{
    position: relative;
    z-index: 3;
    padding: 14px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      right: 100%;
      margin-right: -190px;
      clip-path: polygon(0 0, 100% 0, calc(100% - 46px) 100%, 0% 100%);
      height: calc(100% + 27px);
      width: 30000px;
      background-color: #f7f8fa;
    }
  }
  &__logo{
    margin-right: 77px;
    width: 132px;
    display: block;
    flex-shrink: 0;
    position: relative;
    z-index: 2;
    img{
      width: 100%;
      display: block;
    }
  }
}

.menu{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-grow: 1;
}

.nav{
  position: absolute;
  width: calc(100% + 40px);
  left: -20px;
  top: 100%;
  background-color: #363636;
  box-shadow: 2px 3px 8px 0px rgba(0,0,0,0.14);
  border-radius: 4px;
  padding: 0 34px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 61px;
  color: #fff;
  &__link{
    font-size: 14px;
    line-height: 22px;
    text-transform: uppercase;
    letter-spacing: 0.02em;
    &:hover{
      color: #ffb15a;
    }
    &--main{
      span{
        display: none;
      }
      img{
        width: 16px;
      }
    }
  }
}

.feedback{
  padding-left: 24px;
  height: 52px;
  background-color: rgba(255,255,255,0.98);
  border: 1px solid #e3e7e8;
  display: flex;
  align-items: center;
  border-radius: 26px;
  &__phone{
    color: #929292;
    display: flex;
    align-items: center;
    font-size: 15px;
    font-weight: 900;
    img{
      width: 20px;
      margin-right: 10px;
    }
  }
  &__btn{
    border-radius: 26px;
    flex-shrink: 0;
    height: 52px;
    padding: 0 19px;
    font-weight: 700;
    text-transform: uppercase;
    margin-left: 19px;
    span{
      font-size: 13px;
    }
  }
}

.header-info{
  display: flex;
  align-items: center;
  &__icon{
    width: 57px;
    height: 52px;
    border: 1px solid #e3e7e8;
    @include center;
    background-color: #fff;
    border-radius: 26px;
    margin-right: 14px;
    flex-shrink: 0;
    img{
      max-width: 31px;
      max-height: 31px;
    }
  }
  &__text{
    font-size: 14px;
    line-height: 17px;
    font-weight: 300;
    letter-spacing: 0.02em;
    color: #6b6b6b;
  }
}

.burger{
  display: none;
}
