.advantages{
  padding: 90px 0 45px;
  background-color: #ebecee;
  &__top{
    text-align: center;
  }
  &__title{
    margin-top: 10px;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 8px));
    gap: 10px;
    margin-top: 30px;
  }
}

.advantage-card{
  padding: 26px 29px 40px;
  background-color: #ffffff;
  box-shadow: 0px 0px 104px 16px #f1f3f7;
  border-radius: 10px;
  &__title{
    font-size: 20px;
    line-height: 125%;
    font-weight: 500;
    font-family: $font2;
    min-height: 50px;
  }
  &__icon{
    height: 74px;
    margin: 19px 0 0 -13px;
    img{
      display: block;
      height: 100%;
    }
  }
  &__desc{
    font-size: 15px;
    line-height: 160%;
    letter-spacing: 0.02em;
    margin-top: 23px;
    padding-top: 23px;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 30px;
      height: 2px;
      top: -1px;
      left: 0;
      background: #000;
    }
  }
}

.works{
  padding: 45px 0 90px;
  background-color: #ebecee;
  &__title{
    text-align: center;
  }
}

.works-slider{
  .swiper{
    &-container{
      overflow: hidden;
    }
  }
}

.work-card{
  padding: 32px;
  border: 1px solid #bebebe;
  &__img{
    display: block;
    @include imgCover(91.8%);
  }
}

.categories{
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  padding: 66px 0;
  background-color: #f6f7f9;
  &__title{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: 50% 50%;
    margin-top: 40px;
    gap: 40px 0;
  }
}

.category-card{
  padding-bottom: 40px;
  border-bottom: 1px solid #e4e5e8;
  display: grid;
  grid-template-columns: 112px calc(100% - 128px);
  gap: 16px;
  align-items: center;
  @media screen and (min-width: 993px){
    &:nth-child(odd){
      padding-right: 29px;
    }
    &:nth-child(even){
      padding-left: 29px;
    }
    &:nth-last-child(1),
    &:nth-last-child(2){
      padding-bottom: 0;
      border-bottom: none;
    }
  }
  &__img{
    border-radius: 3px;
    @include imgCover(136.6%);
  }
  &__title{
    font-size: 20px;
    line-height: 125%;
    font-weight: 400;
    font-family: $font2;
  }
  &__desc{
    font-size: 15px;
    line-height: 133%;
    margin-top: 10px;
    @include truncate(3);
  }
  &__nav{
    display: flex;
    margin-top: 9px;
  }
  &__btn{
    height: 34px;
    padding: 0 20px;
    font-weight: 700;
    border-radius: 17px;
    span{
      font-size: 13px;
    }
  }
}

.awards{
  padding: 90px 0 33px;
  &__title{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 128px / 5));
    gap: 32px;
    margin-top: 48px;
  }
}

.award-card{
  &__top{
    display: flex;
    align-items: center;
  }
  &__icon{
    flex-shrink: 0;
    width: 92px;
    height: 92px;
    background-color: #474573;
    background-image: linear-gradient(0.0deg, #20adea 6%, #3cbaf1 94%);
    border-radius: 15px;
    @include center;
    margin-right: 17px;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      top: -8px;
      right: -12px;
      width: 37px;
      height: 35px;
      background-image: url(../img/star.png);
      @include bgImgSize(100%);
    }
    img{
      max-width: 57px;
      max-height: 57px;
    }
  }
  &__title{
    font-size: 20px;
    line-height: 125%;
    font-weight: 500;
    font-family: $font2;
  }
  &__desc{
    margin-top: 18px;
    font-size: 15px;
    line-height: 165%;
    letter-spacing: 0.01em;
  }
}

.how-work{
  padding: 33px 0 80px;
  overflow: hidden;
  &__top{
    text-align: center;
  }
  &__desc{
    margin-top: 10px;
  }
  &__bottom{
    width: 100%;
    max-width: 1264px;
    margin: 0 auto;
  }
  &__grid{
    margin-top: 40px;
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 95px);
  }
  &__grid2{
    margin-top: 27px;
    display: flex;
    flex-direction: row-reverse;
  }
  @media screen and (min-width: 993px){
    &__grid{
      position: relative;
      &:after{
        content: '';
        position: absolute;
        top: 100%;
        right: 122px;
        width: 14px;
        height: 23px;
        margin-top: -7px;
        background-image: url(../img/line_arrow2.png);
        background-size: 100% auto;
        background-repeat: no-repeat;
        background-position: center;
      }
    }
    .step-item{
      &:last-child{
        .step-item__top:before{
          display: none;
        }
      }
    }
    &__grid2{
      width: calc(100% + 40px);
      .step-item{
        width: calc(20% - 40px);
        margin: 0 40px 0 0;
        &__top{
          &:before{
            background-size: 100% auto;
            background-image: url(../img/line_arrow3.png);
            width: 32px;
            left: unset;
            right: 100%;
            margin-right: 4px;
          }
        }
      }
    }
  }
}

.step-item{
  width: calc(25% - 95px);
  margin: 0 95px 0 0;
  text-align: center;
  &__top{
    border: 1px dotted #3b3663;
    min-height: 60px;
    padding: 5px;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      left: 100%;
      margin-left: 6px;
      transform: translateY(-50%);
      width: 83px;
      height: 15px;
      background-image: url(../img/line_arrow.png);
      background-size: auto 100%;
      background-repeat: no-repeat;
      background-position: right top;
    }
  }
  &__inner{
    background-color: #ec2451;
    background-image: linear-gradient(102.0deg, #ffb15a 7%, #ffa540 93%);
    color: #fff;
    border-radius: 24.5px;
    flex-grow: 1;
    min-height: 100%;
    @include center;
    padding: 0 14px;
    font-size: 17px;
    span{
      letter-spacing: 0.01em;
    }
    img{
      width: 43px;
      margin-right: 16px;
      flex-shrink: 0;
    }
  }
  &__desc{
    font-size: 14px;
    line-height: 105%;
    margin-top: 8px;
    padding: 0 15px;
  }
}
