.footer{
  padding: 35px 0 32px;
  border-top: 1px solid #e9ecee;
  background-color: #f7f8fa;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__col{
    margin-right: 40px;
    &:last-child{
      margin-right: 0;
    }
  }
  &__logo{
    display: block;
    width: 122px;
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    font-size: 14px;
    line-height: 178%;
    letter-spacing: 0.02em;
    margin-top: 22px;
    min-width: 310px;
    a{
      white-space: nowrap;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid #e3e7e8;
    margin-top: 45px;
    padding-top: 30px;
  }
  &__by,
  &__c{
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.02em;
    color: #0c0927;
  }
}

.footer-nav{
  max-width: 480px;
  column-count: 2;
  li{
    width: 100%;
    display: inline-block;
    margin-bottom: 20px;
  }
  a{
    font-size: 14px;
    line-height: 157%;
    letter-spacing: 0.02em;
  }
}

.footer-subscribe{
  width: 352px;
  position: relative;
  &__btn{
    position: absolute;
    top: 0;
    right: 0;
    width: 172px;
    height: 52px;
    border-radius: 26px;
    font-weight: 700;
    padding: 0 20px;
    span{
      font-size: 13px;
    }
  }
  &__field{
    position: relative;
    input{
      width: 100%;
      height: 52px;
      border: 1px solid #e3e7e8;
      border-radius: 26px;
      padding-right: 180px;
      padding-left: 63px;
      font-size: 15px;
      font-weight: 500;
      font-family: $font2;
      &::placeholder{
        color: #a4a4aa;
      }
    }
  }
  &__icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 24px;
    width: 1.25em;
  }
}

.footer-socials{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-top: 26px;
  &__title{
    font-size: 14px;
    letter-spacing: 0.02em;
    color: #5b5b5b;
    margin-right: 7px;
  }
  &__item{
    width: 47px;
    height: 47px;
    border: 1px solid #a5a5ab;
    border-radius: 50%;
    @include center;
    margin-left: 9px;
    img{
      max-width: 28px;
      max-height: 28px;
    }
    &:hover{
      border-color: #ffb15a;
    }
  }
}

.scroll-up {
  z-index: 22;
  position: fixed;
  right: 50px;
  bottom: 40px;
  width: 59px;
  height: 59px;
  background-color: #ffffff;
  box-shadow: 1px 2px 3px 0px rgba(72,39,9,0.1);
  transition: all .3s;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
  //opacity: 0;
  //visibility: hidden;
  &:hover {
    //background-color: $primaryHover;
    //border-color: $primaryHover;
    path {
      stroke: $black;
    }
  }
  img{
    width: 27px;
  }
  &--active {
    visibility: visible;
    opacity: 1;
  }
}
