@media screen and (max-width: 1400px){
  .awards{
    &__grid{
      grid-template-columns: repeat(5,calc(20% - 80px / 5));
      gap: 40px 20px;
    }
  }

  .award-card{
    &__icon{
      width: 75px;
      height: 75px;
      margin-right: 10px;
    }
    &__title{
      font-size: 18px;
    }
  }

  .header{
    &__inner{
      &:before{
        margin-right: -144px;
      }
    }
    &__logo{
      width: 110px;
      margin-right: 40px;
    }
  }

  .header-info{
    &__icon{
      margin-right: 7px;
      width: 40px;
      height: 40px;
      img{
        max-width: 22px;
        max-height: 22px;
      }
    }
  }

  .nav{
    &__link{
      font-size: 12px;
    }
  }
}


@media screen and (max-width: 1200px){
  .catalog-item{
    &__inner{
      grid-template-columns: 400px calc(100% - 430px);
      gap: 30px;
    }
  }

  .gallery{
    &__small {
      .swiper {
        &-slide {
          width: calc(100% / 3);
        }
      }
    }
  }

  .catalog-info{
    padding: 14px;
    border-radius: 10px;
    .tab-content{
      padding: 14px;
    }
  }

  .services-slider{
    .swiper{
      &-slide{
        width: calc(100% / 3);
      }
    }
  }

  .qa-card{
    &__hidden{
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  .services-page{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 32px / 3));
    }
  }

  .site-item{
    margin-right: 20px;
  }

  .portfolio{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 16px));
    }
  }

  .team{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 8px));
      gap: 16px;
    }
  }

  .team-card{
    grid-template-columns: 150px calc(100% - 160px);
    gap: 10px;
  }

  .certificates{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 16px));
      gap: 24px;
      margin-top: 40px;
    }
  }

  .contacts{
    &__col{
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  .map{
    margin-top: 40px;
  }

  .about-gallery{
    grid-template-columns: repeat(3,calc(100% / 3 - 40px / 3));
    gap: 20px;
  }

  .main-slider{
    .swiper-button{
      width: 40px;
      height: 40px;
      img{
        width: 20px;
      }
      &-prev{
        left: 10px;
      }
      &-next{
        right: 10px;
      }
    }
  }

  .main-banner{
    padding: 30px 60px;
  }

  .advantages{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 20px / 3));
    }
  }

  .awards{
    &__grid{
      grid-template-columns: repeat(4,calc(25% - 15px));
    }
  }

  .how-work{
    &__grid{
      width: calc(100% + 40px);
    }
  }

  .step-item{
    width: calc(25% - 40px);
    margin: 0 40px 0 0;
    &__top{
      &:before{
        width: 32px;
        margin-left: 4px;
      }
    }
    &__desc{
      padding: 0;
    }
  }

  .footer-nav{
    column-count: 1;
    max-width: unset;
    li{
      margin-bottom: 10px;
    }
  }

  .feedback{
    height: 44px;
    padding-left: 10px;
    border-radius: 22px;
    &__btn{
      border-radius: 22px;
      height: 44px;
      padding: 0 10px;
      margin-left: 10px;
      span{
        font-size: 12px;
      }
    }
    &__phone{
      font-size: 13px;
    }
  }

  .header-info{
    &__text{
      font-size: 12px;
    }
  }
}


@media screen and (max-width: 1100px){
  .nav{
    padding: 0 10px;
    &__link{
      font-size: 11px;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    overflow: hidden;
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .gallery{
    &__small{
      display: none;
    }
    &__big{
      .swiper{
        overflow: unset;
        &-slide{
          padding-right: 10px;
          width: 300px;
        }
      }
    }
  }

  .services-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .services-page__text, .text35{
    font-size: 28px;
  }

  .page-block{
    padding: 30px 0 60px;
  }

  .faq{
    &__grid{
      grid-template-columns: 100%;
      margin-top: 25px;
    }
    &__desc{
      margin-top: 10px;
    }
  }

  .services-page{
    &__grid{
      margin-top: 25px;
      grid-template-columns: repeat(2,calc(50% - 8px));
    }
    &__text{
      margin-top: 40px;
    }
  }

  .consult{
    border-radius: 16px;
    margin-top: 40px;
    &__col1,
    &__col2{
      padding: 40px;
    }
  }

  .reviews{
    &__inner{
      padding: 30px 0 60px;
    }
    &__items{
      margin-top: 16px;
      width: calc(100% + 16px);
      max-width: unset;
    }
    &__img{
      display: none;
    }
  }

  .site-item{
    margin-right: 16px;
    width: calc(100% / 3 - 16px);
  }

  .portfolio{
    &__grid{
      margin-top: 25px;
      grid-template-columns: repeat(2,calc(50% - 6px));
      gap: 12px;
    }
  }

  .team{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .certificate-card{
    padding: 15px;
  }

  .contacts{
    &__grid{
      display: grid;
      grid-template-columns: 100%;
      gap: 20px;
      margin-top: 30px;
    }
    &__col{
      padding: 0 !important;
      max-width: 100% !important;
      min-height: unset;
      &:not(:last-child){
        border-bottom: 1px solid #ccc;
        padding-bottom: 20px !important;
      }
      &:after{
        display: none;
      }
    }
  }

  .contacts-item{
    @include lastM(0 0 25px);
    &__desc{
      min-height: unset;
    }
  }

  .map{
    height: 350px;
  }

  .about-gallery{
    margin-top: 25px;
  }

  .about{
    &__text{
      margin-top: 30px;
    }
  }

  .review-card{
    grid-template-columns: 100%;
    gap: 20px;
    &__left{
      padding-right: 0;
    }
    &__right{
      padding: 20px 0 0;
      border-left: none;
      border-top: 1px solid #bebebe;
    }
  }

  .main{
    padding: 0 0 20px;
  }

  .main-banner{
    padding: 30px 60px;
  }

  .print-card{
    width: 33%;
  }

  .order{
    padding: 30px 0;
    &__fields{
      grid-template-columns: 100%;
      gap: 8px;
    }
    &__nav{
      margin-top: 12px;
    }
    &__btn{
      width: 100%;
    }
  }

  .advantages{
    padding: 40px 0 20px;
    &__grid{
      margin-top: 20px;
      grid-template-columns: repeat(2,calc(50% - 5px));
    }
  }

  .works{
    padding: 20px 0 40px;
  }

  .works-slider{
    margin-top: 20px;
    .swiper-nav{
      margin-bottom: 27px;
    }
  }

  .work-card{
    padding: 12px;
  }

  .categories{
    padding: 30px 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 20px;
    }
  }

  .category-card{
    padding-bottom: 20px;
    &:last-child{
      padding-bottom: 0;
      border-bottom: none;
    }
  }

  .awards{
    padding: 25px 0;
    &__grid{
      margin-top: 25px;
      grid-template-columns: repeat(3,calc(100% / 3 - 40px / 3));
    }
  }

  .how-work{
    padding: 20px 0 40px;
    &__grid{
      width: 100%;
      display: block;
      margin-top: 25px;
    }
    &__grid2{
      display: block;
      .step-item{
        &:last-child{
          margin-bottom: 0;
          &:after{
            display: none;
          }
        }
      }
    }
  }

  .step-item{
    margin: 0 0 20px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    &:after{
      content: '';
      display: block;
      top: 100%;
      right: 122px;
      width: 14px;
      height: 23px;
      margin: 10px auto 0;
      background-image: url(../img/line_arrow2.png);
      background-size: 100% auto;
      background-repeat: no-repeat;
      background-position: center;
    }
    &__top{
      min-width: 60%;
      &:before{
        display: none;
      }
    }
  }

  .footer{
    &__top{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 16px));
      gap: 32px;
    }
    &__col{
      margin: 0;
    }
    &__col3{
      grid-column: 1/-1;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 16px));
      gap: 32px;
    }
    &__bottom{
      display: block;
      margin-top: 30px;
    }
    &__by{
      margin-top: 20px;
    }
  }

  .footer-socials{
    margin: 0;
    justify-content: unset;
  }

  .header{
    padding-bottom: 0;
    //padding-bottom: 20px;
    margin-bottom: 20px;
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
    &__inner{
      &:before{
        height: 100%;
        margin-right: -170px;
      }
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    flex-direction: column;
    align-items: flex-start;
    justify-content: unset;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    padding: 80px 16px 16px;
    background-color: #363636;
    color: #fff;
    overflow-y: auto;
    z-index: 20;
  }

  .nav{
    position: static;
    background-color: transparent;
    box-shadow: unset;
    padding: 0;
    flex-direction: column;
    align-items: flex-start;
    &__link{
      margin: 0 0 20px;
      font-size: 16px;
      display: flex;
      align-items: center;
      span{
        display: block;
      }
      img{
        margin-right: 10px;
      }
    }
  }

  .burger{
    border-radius: 50%;
    z-index: 21;
    width: 40px;
    height: 40px;
    @include center;
    position: relative;
    img{
      transition: all .3s;
      @include centerAbsolute;
      width: 25px;
    }
    ._view2{
      opacity: 0;
    }
  }

  .header-info{
    margin: 0 0 20px;
    &__text{
      color:  #fff;
      font-size: 14px;
    }
  }
  .feedback{
    width: 100%;
    &__btn{
      flex-grow: 1;
    }
  }

  .scroll-up{
    width: 40px;
    height: 40px;
    right: 15px;
    bottom: 25px;
    img{
      width: 20px;
    }
  }
}


@media screen and (max-width: 768px){
  .services-page{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .catalog-info{
    border-radius: 0;
    padding: 10px;
    .tabs{
      width: calc(100% + 20px);
      margin-left: -10px;
      padding: 0 10px;
      overflow-x: auto;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
    .tab{
      flex-shrink: 0;
    }
  }

  .consult{
    grid-template-columns: 100%;
    &__desc,
    &__check{
      max-width: 100%;
    }
  }

  .site-item{
    width: calc(50% - 16px);
  }

  .certificates{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 12px));
    }
  }

  .order-form{
    padding: 20px 15px 0;
    &__field{
      grid-template-columns: 100%;
      .order-form__title,
      label{
        padding: 0;
        text-align: left;
      }
    }
    &__count{
      display: block;
      &-text{
        margin: 8px 0 0;
      }
    }
  }

  .review-card{
    padding: 20px;
  }

  .print{
    padding: 20px 0;
  }

  .print-card{
    margin-top: 25px;
    width: 50%;
    padding: 0 15px;
    &__img{
      width: 130px;
      height: 130px;
      padding: 10px;
    }
    &__link{
      bottom: 0;
    }
    &__title{
      font-size: 14px;
    }
  }

  .file-input2{
    flex-direction: column;
    text-align: center;
    &__btn{
      margin: 0 0 10px;
      order: -1;
    }
    &__title{
      font-size: 12px;
    }
  }

  .awards{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 10px));
    }
  }

  .footer{
    &__col3{
      grid-template-columns: 100%;
    }
    &__info{
      min-width: auto;
    }
  }

  .footer-subscribe{
    width: 100%;
  }
}


@media screen and (max-width: 576px){
  .m-text h3,
  .text35{
    font-size: 28px;
  }

  .gallery{
    &__big{
      .swiper{
        &-slide{
          max-width: 250px;
          width: 80%;
        }
      }
    }
  }

  .services{
    &__title{
      font-size: 28px;
    }
  }

  .services-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }

  .consult{
    grid-template-columns: 100%;
    &__col1,
    &__col2{
      padding: 30px 20px;
    }
  }

  .consult-form{
    &__fields{
      margin-top: 25px;
      gap: 20px;
    }
    &__btn{
      margin-top: 25px;
    }
  }

  .site-item{
    width: calc(100% - 16px);
  }

  .portfolio-modal{
    &__desc{
      padding: 8px 15px;
    }
  }

  .portfolio{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .team-card{
    align-items: flex-start;
    grid-template-columns: 120px calc(100% - 130px);
    &__name{
      font-size: 20px;
    }
    &__prof{
      font-size: 14px;
    }
    &__desc{
      font-size: 14px;
    }
  }

  .certificates{
    &__grid{
      margin-top: 20px;
      grid-template-columns: repeat(2,calc(50% - 6px));
      gap: 12px;
    }
  }

  .certificate-card{
    padding: 5px;
  }

  .about-gallery{
    grid-template-columns: 100%;
    gap: 12px;
  }

  .gallery-item {
    &._big {
      grid-column: unset;
      grid-row: unset;
    }
  }

  .file-input{
    padding: 8px;
    flex-direction: column;
    &__hint{
      margin: 8px 0 0;
    }
  }

  .call-form{
    padding: 20px 15px 0;
  }

  .review-card{
    &__top{
      display: block;
    }
    .rating-block{
      margin: 10px 0 0;
    }
  }

  .main{
    overflow: hidden;
    padding-bottom: 50px;
    .container{
      padding: 0;
    }
  }

  .main-slider{
    overflow: unset;
    .swiper-button{
      top: 100%;
      margin: 5px 0 0;
      &.swiper-button-disabled{
        background: gray;
      }
      &-prev{
        left: 50%;
        transform: translateX(-110%);
      }
      &-next{
        right: 50%;
        transform: translateX(110%);
      }
    }
  }

  .main-banner{
    padding: 25px 16px;
    min-height: 450px;
    &:after{
      border-radius: 10px;
    }
    &__title{
      font-size: 28px;
    }
    &__desc{
      font-size: 14px;
    }
    &__img{
      img{
        border-radius: 10px;
      }
    }
  }

  .order,
  .print{
    &__title{
      font-size: 20px;
    }
  }

  .print-card{
    padding: 0 5px;
    &__title{
      font-size: 12px;
    }
  }

  .order{
    &__desc{
      font-size: 14px;
    }
    &__fields{
      margin-top: 20px;
    }
  }

  .advantages{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .works{
    overflow: hidden;
  }

  .works-slider{
    .swiper-container{
      overflow: unset;
    }
  }

  .awards{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .step-item{
    &__top{
      width: 100%;
    }
  }

  .footer{
    &__top{
      grid-template-columns: 100%;
    }
  }

  .footer-subscribe{
    @include grid100gap(10px);
    &__btn{
      position: static;
      width: 100%;
    }
    &__field{
      input{
        padding-right: 20px;
      }
    }
  }

  .footer-socials{
    flex-wrap: wrap;
    &__title{
      width: 100%;
      margin: 0 0 10px;
    }
    &__item{
      margin: 0 10px 0 0;
    }
  }
}
