.team{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 20px));
    gap: 40px;
    margin-top: 45px;
  }
}

.team-card{
  display: grid;
  grid-template-columns: 183px calc(100% - 207px);
  gap: 24px;
  align-items: center;
  &__img{
    @include imgCover(100%);
    border-radius: 50%;
  }
  &__name{
    font-size: 27px;
    line-height: 120%;
    font-weight: 700;
  }
  &__prof{
    font-size: 15px;
    line-height: 130%;
    letter-spacing: 0.01em;
    color: #dc811b;
    margin-top: 8px;
  }
  &__desc{
    font-size: 15px;
    line-height: 148%;
    margin-top: 8px;
  }
}
