.contacts{
  &__grid{
    display: flex;
    margin-top: 50px;
  }
  &__col{
    flex-grow: 1;
    padding-right: 70px;
    padding-left: 70px;
    position: relative;
    min-height: 252px;
    padding-top: 22px;
    &:after{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      background: #cccccc;
      width: 1px;
      height: 252px;
      max-width: 100%;
    }
    &:first-child{
      padding-left: 0;
      max-width: 230px;
    }
    &:nth-child(2){
      padding-right: 30px;
      max-width: 360px;
    }
    &:nth-child(3){
      max-width: 512px;
    }
    &:last-child{
      padding-right: 0;
      &:after{
        display: none;
      }
    }
  }
}

.contacts-item{
  @include grid100gap(16px);
  @include lastM(0 0 40px);
  &__title{
    font-size: 16px;
    line-height: 110%;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__desc{
    font-size: 19px;
    line-height: 131%;
    min-height: 50px;
    a{
      white-space: nowrap;
    }
  }
  &__nav{
    display: flex;
  }
  &__btn{
    padding: 0 29px;
    height: 34px;
    font-weight: 700;
    border-radius: 17px;
    span{
      font-size: 13px;
    }
  }
}

.map{
  margin-top: 60px;
  height: 493px;
}
