.page-block{
  padding: 50px 0 100px;
}

.m-text{
  h3{
    font-weight: 700;
    margin-bottom: 14px;
  }
  p{
    font-size: 17px;
    line-height: 147%;
    @include lastM(0 0 20px);
  }
  hr{
    height: 30px;
    opacity: 0;
    display: block;
    margin: 0;
  }
}

.services-page{
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 12px));
    gap: 16px;
    margin-top: 40px;
  }
  &__text{
    max-width: 1130px;
    margin-top: 80px;
  }
}

.consult{
  margin: 80px auto 0;
  background-color: #ffffff;
  box-shadow: 0px 10px 38px 0px rgba(0,0,0,0.18);
  border-radius: 33px;
  overflow: hidden;
  display: grid;
  grid-template-columns: 50% 50%;
  width: 947px;
  max-width: 100%;
  &__col1{
    padding: 80px 74px;
    background: #65b4d6;
    color: #fff;
    display: flex;
    flex-direction: column;
  }
  &__col2{
    padding: 80px;
  }
  &__title{
    font-size: 30px;
    line-height: 120%;
    padding-bottom: 25px;
    position: relative;
    font-weight: 500;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 70px;
      height: 3px;
      background-color: #ffffff;
      max-width: 100%;
    }
  }
  &__desc{
    font-size: 15px;
    line-height: 167%;
    letter-spacing: 0.02em;
    margin-top: 40px;
    max-width: 261px;
  }
  &__check{
    max-width: 261px;
    margin-top: auto;
    padding-top: 40px;
  }
  .m-check{
    label{
      font-size: 15px;
      line-height: 133%;
      &:before{
        background-color: #fff;
        border: none;
        background-image: url(../img/check2.png);
      }
    }
    input:checked~label{
      &:before{
        background-size: 14px auto;
      }
    }
  }
}


.consult-form{
  &__title{
    text-align: center;
    width: 300px;
    max-width: 100%;
    margin: 0 auto;
    font-size: 17px;
    line-height: 147%;
    color: #646466;
  }
  &__fields{
    margin-top: 45px;
    @include grid100gap(40px);
  }
  &__btn{
    margin: 44px auto 0;
    border-radius: 26px;
    font-weight: 700;
    span{
      font-size: 13px;
    }
  }
}
