.faq{
  padding: 50px 0 100px;
  &__top{
    text-align: center;
    width: 880px;
    max-width: 100%;
    margin: 0 auto;
  }
  &__desc{
    margin-top: 22px;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 10px));
    gap: 4px 20px;
    margin-top: 40px;
  }
  &__items{
    @include grid100gap(4px);
  }
}

.qa-card{
  &._active{
    .qa-card__btn img{
      transform: rotate(90deg);
    }
  }
  &__show{
    display: flex;
    align-items: center;
    padding: 10px 20px;
    background-color: #ffffff;
    box-shadow: 0px 6px 18px 0px rgba(0,0,0,0.1);
    border-radius: 3px;
    min-height: 62px;
    position: relative;
    z-index: 2;
    cursor: pointer;
  }
  &__btn{
    border: 2px solid #ffa643;
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    margin-right: 16px;
    border-radius: 50%;
    @include center;
    img{
      width: 8px;
      transition: all .3s;
    }
  }
  &__hidden{
    display: none;
    padding: 55px 40px 15px 57px;
    margin-top: -44px;
    background-color: #f6f7f9;
    border-radius: 3px;
  }
  &__text{
    font-size: 16px;
    line-height: 138%;
  }
}
